
import XHeader from 'vux/src/components/x-header/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapGetters } from "vuex";

export default {
directives: {
TransferDom
},
components: {
XHeader,
Previewer
},
computed: {
...mapGetters(["helpDetail"])
},
created() {},
activated() {
this.$store.dispatch("getHelpDetail", {
id: this.$route.params.helpid
});
},
deactivated() {
this.infos = {};
this.list = [];
},
watch: {
helpDetail(Value) {
this.infos = Value;
if (Value.images) {
Value.images.forEach((item, index) => {
this.list.push({ src: item, msrc: Value.thumbs[index] });
});
}
}
},
data() {
return {
infos: {},

list: [],
options: {
getThumbBoundsFn(index) {

let thumbnail = document.querySelectorAll(".previewer-demo-img")[
index
];

let pageYScroll =
window.pageYOffset || document.documentElement.scrollTop;


let rect = thumbnail.getBoundingClientRect();

return { x: rect.left, y: rect.top + pageYScroll, w: rect.width };


}
}
};
},
methods: {
show(index) {
this.$refs.previewer.show(index);
}
}
};



